/** Media Queries **/
.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  background: #000;
  color: #fff;
}

p {
  color: #fff;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
  color: #fff;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Montserrat", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

::-webkit-scrollbar {
  display: none;
}

.nav {
  display: flex;
  height: 5rem;
  justify-content: space-between;
  margin: 3rem 2rem;
  position: relative;
}
.nav::after {
  position: absolute;
  content: "";
  background: #fff;
  height: 0.2rem;
  border-radius: 1rem;
  width: 100%;
  top: -1rem;
}
.nav::before {
  position: absolute;
  content: "";
  background: #fff;
  height: 0.2rem;
  border-radius: 1rem;
  width: 100%;
  bottom: -2rem;
}
.nav p {
  position: absolute;
  background: #000;
  padding-right: 1rem;
  top: 3.2rem;
  font-size: 1.8rem;
}
.nav .nav__logo {
  display: flex;
  width: 9rem;
}
.nav .nav__logo img {
  width: auto;
  height: auto;
  margin: 0.7rem 0;
}

.nav__logo img, .nav__icon img {
  height: 100%;
}

.nav__list {
  display: flex;
  align-items: center;
  gap: 3rem;
  list-style: none;
  padding: 0;
  margin: 0;
  text-transform: capitalize;
  font-size: 2rem;
}
.nav__list .nav__item--select {
  font-weight: 700;
}

.nav__item:hover {
  text-decoration: underline;
}

.slider {
  width: 100%;
  height: auto;
  margin-top: 5rem;
  overflow: hidden;
}
.slider .slide-track {
  display: flex;
  animation: scroll 5s linear infinite;
  -webkit-animation: scroll 10s linear infinite;
  width: 400%;
}
.slider .slide {
  background-image: url("../img/patron-lineas.svg");
  height: 40px;
  background-repeat: repeat;
  width: 100%;
  background-size: contain;
}

@keyframes scroll {
  0% {
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }
  100% {
    -webkit-transform: translateX(-1400px);
    transform: translateX(-1400px);
  }
}
.header {
  height: 80vh;
  margin: 0 2rem;
  display: grid;
  grid-template-columns: 2fr 1fr;
  align-items: center;
}
.header h2 {
  font-weight: 900;
  text-transform: capitalize;
  font-size: 8rem;
  text-align: left;
}
.header img {
  height: 25rem;
  width: auto;
  margin-bottom: 15rem;
  margin-right: 15rem;
}

.main {
  text-transform: capitalize;
}

.main__about h3 {
  font-size: 30rem;
  font-weight: 700;
  text-align: end;
}
.main__about hr {
  margin: 6rem 0;
}
.main__about a {
  font-size: 4.4rem;
  font-weight: 700;
  position: relative;
}
.main__about a:after {
  content: "";
  width: 100%;
  height: 0.3rem;
  background: #fff;
  border-radius: 10rem;
  position: absolute;
  bottom: 0;
  left: 0;
}

.about__contenido {
  padding-left: 5rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
.about__contenido p {
  font-size: 3.2rem;
}
.about__contenido p:first-child {
  font-weight: 700;
}

.about__line {
  display: flex;
  align-items: end;
  justify-content: end;
}
.about__line p {
  font-size: 30rem;
  font-weight: 700;
  margin: 0;
  padding-right: 20rem;
}

.main__skills h3 {
  font-size: 30rem;
  width: 100rem;
  line-height: 30rem;
  font-weight: 700;
  text-align: start;
  position: relative;
  left: -5rem;
}

.main__portfolio h3 {
  font-size: 30rem;
  line-height: 30rem;
  font-weight: 700;
  text-align: end;
}

.cards {
  margin: 0 5rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 3rem;
}

.card {
  display: flex;
  flex-direction: column;
}
.card p {
  margin: 0;
}
.card div {
  border-radius: 1rem;
  border: 0.1rem solid #fff;
  padding: 2rem;
}